.root{font-family:"Open Sans",sans-serif !important}.root::before,.root::after{content:none !important}.root .input{display:block;height:initial !important;min-height:17px;padding:8px 14px 7px;font-size:12px;line-height:17px;text-align:left;color:#33333d;font-family:"Open Sans",sans-serif;background-color:rgba(123,138,157,0.12);border:1px solid transparent;border-radius:4px;resize:none;outline:none;box-shadow:none !important;transition:border-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms}.root .input:focus{border-color:rgba(123,138,157,0.5);background-color:rgba(123,138,157,0.06) !important;border-radius:4px !important}.root .input:focus+.icon{color:rgba(123,138,157,0.5)}.root .input::placeholder{color:#99a0ab}.root .input.hasIcon{padding-right:32px}.root .input[type="search"]{appearance:textfield}.root .input[type="search"]::-webkit-search-decoration,.root .input[type="search"]::-webkit-search-cancel-button,.root .input[type="search"]::-webkit-search-results-button,.root .input[type="search"]::-webkit-search-results-decoration{display:none}.root.error .input{background-color:rgba(208,2,27,0.14) !important;border-color:#d0021b;color:#d0021b}.root.error .input::placeholder{color:#d0021b}.root.error .input+.icon{color:#d0021b}.root .icon{width:21px;height:21px;position:absolute;right:9px;top:50%;transform:translateY(-50%);pointer-events:none;color:#d7dade;transition:color 250ms cubic-bezier(0.4, 0, 0.2, 1),fill 250ms cubic-bezier(0.4, 0, 0.2, 1)}



